import React from "react";
import "./Detail.css"
import "../components/Movie.css"

class Detail extends React.Component{
    componentDidMount() {
        const {location, history} = this.props;
        if(location.state === undefined){
            history.push("/");
        }
    }
    render() {
        const {location} = this.props;
        if(location.state) {
            return (
                <div>
                    <div className="detail__back">
                    <img src={location.state.poster} alt={location.state.title} />
                    </div>
                    <div className="detail__container">
                    <img className="detail__poster" src={location.state.poster} alt={location.state.title} />
                    <h3 className="movie__title">{location.state.title}</h3>
                    <h5 className="movie__year">{location.state.year}</h5>
                <ul className="movie__geners">
                    {location.state.genres.map((genre, index) => <li key={index} className="genres__genre">  {genre} </li> )}
                </ul>
                <p className="movie__summary">{location.state.summary}</p>
                    </div>
                </div>
            )
        }else{
            return null;
        }
    }
}

export default Detail;